@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap);
*{
margin:0px;
padding: 0px;
font-family: 'Poppins', sans-serif;
}

.navbar{

}
.navbar__nav{
display: flex;
justify-content: space-around;
align-items: center;
height:60px;
border-bottom: 1px solid gray;

}
.nav__title{
   
font-size: 23px;
cursor: pointer;
/* padding:5px; */
padding:0px 20px 0px 20px;
border: 1px solid white ;
}
.nav__title:hover{
    border: 1px dotted black;
    
}
.nav__unlist{
display: flex;
list-style: none;
/* border: 1px solid black; */
font-size: 18px;
padding:2px;


}
.nav__unlist li{
    
    cursor: pointer;
   margin-right:30px;
    padding:10px;
    border: 1px solid white ;
}
.nav__unlist li:hover{
    border: 1px dotted black;
}

.nav__unlist a{
text-decoration: none;
color: black;
}
.nav__unlist a:active{
    color: black;
}


.nav__unlist__home{

}
.nav__unlist__feedback{
    
}
.nav__unlist__login{

}
.nav__unlist__singup{

}
.nav__unlist__logout{

}

@media(max-width :626px){
    .nav__title{
        font-size: 20px;
        padding:0px ;
        margin-left: 5px;
    }
}
.body{
    display: flex;
    justify-content: space-evenly;
}
.body__first{
    border: 1px solid black;
    width: 300px;
    height: 200px;
    margin-top: 40px;
    text-align:center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}
.body__first h1{
    font-weight: 300;
}
.body__second{
    border: 1px solid black;
    width: 300px;
    height: 200px;
    margin-top: 40px;
    text-align:center;
    display: flex;
    flex-direction: column;

    justify-content: space-between;
}
.body__second h1{
    font-weight: 300;
}

.body__start{
    text-decoration: none;
    color:black;
    border: 1px solid black;
    padding: 10px;
    margin: 5px;
}
.body__start:hover{

}
.body__start:active{
    color:black;
}

@media(max-width :626px){
    .body{
        flex-direction: column;
        align-items: center;
    }
}
.feed{
display: flex;
flex-direction: column;

border: 1px solid black;
width:450px;
margin:30px auto;
padding:20px;

}
.feed h1{
    text-align: center;
    margin-bottom: 15px;
    font-weight: 500;
}
.feed__first ul{
    display: flex;
    justify-content: space-around;
 list-style: none;
 cursor: pointer;
 margin-top: 10px;
}
.feed__first label{

}
.feed__second{
    margin:30px 0px 30px 0px;
}
.feed__second ul{
    list-style: none;
    display: flex;
    justify-content: space-around;
    cursor: pointer;
    margin-top: 10px;
}
.feed__second ul li{
    border: 1px solid white ;
padding: 10px;
}
.feed__second ul li:hover{
    border: 1px dotted black;
}
.feed__second label{

}

.feed__third label{

}
.feed__third {
display: flex;
flex-direction: column;
margin:30px 0px 20px 0px;
}
button{
height: 40px;
border-radius: 10px;
border:1px solid gray;
cursor: pointer;
font-size: 15px;
opacity: 1;
}
button:active{
    opacity: 0.8;
}

textarea{
    min-height: 70px;
    resize: vertical;
    font-size: 20px;
    border-radius: 10px;
}
.chat{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.chatbody{
    border:1px solid gray;
    width:99%;
    height:73vh;
    overflow-y: auto;
    margin-top: 5px;
}

.chatbody__message{
  
    
}

.chatbody__section{
display: flex;
margin:5px;
}
.chatbody__section__name{

font-weight: 500;
margin-right:10px;

}
.user1{
    color: blue;
}
.user2{
    color: red;
}
.chatbody__section__message{

}

.chatfoot{
    
    width:99%;
    height:6em;
    border:1px solid gray;
    display: flex;
}
.chatfoot__first{
flex :0.1 1;
border: 1px solid gray;
margin: 7px;
text-align: center;
border-radius: 0px 0px 0px 10px;
cursor: pointer;
padding-top: 10px;

}
.chatfoot__second{
flex :0.8 1;
padding: 7px;
}
.chatfoot__second textarea{
    width:100%;
    border-radius: 0%;
    height: 80px;
    resize: none;
    
}
.chatfoot__third{
flex: 0.1 1;
border: 1px solid gray;
margin: 7px;
text-align: center;
border-radius: 0px 0px 10px 0px;
cursor: pointer;
padding-top: 10px;
}

